import React                    from 'react';

const ImageTitle =({img, title1, title2, divImg})=>{
    img = img || 'mainImg homeImg'
    title1 = title1 || ''
    title2 = title2 || ''
    divImg = divImg || "pagsDivImg"
  

    return <div className={img} >
              <div className={divImg}>
                <h1 className="titleImg ts">{title1}</h1>
                <h1 className=" ts">{title2}</h1>
              </div>
           </div>
}

export default ImageTitle
