         <p>מה אנחנו לא מוכנים לעשות בשביל הילדים. לעבוד בשתי מישרות, להתרוצץ ולטרוח, להיות ערים לילות, ומה לא. נדאג להם לבגדים כמיטב האופנה, לחדר מפנק עם מחשב אישי, לחוגי העשרה בקיצור, שיהיה להם "הכי טוב"...</p>
