import React from 'react';
import Icon from './../components/main/icon';

const CommunityActivity = ()=>{
    return<div style={{
        width: '100%',
        top: '400px',
        }}>
            <div style={{
                backgroundColor: '#f2ece0',
                width: '100%',
                padding: '5%',
            }}>         
            <div class="container">
                <div class="icons">
                    <div style={{height: "60px"}}></div>
                    <Icon  api='/women' title="נשים"/>
                    <Icon  api='/children' title="ילדים"/>
                    <Icon api='/youth' title="נוער"/>
                </div>
            </div>
        </div>
    </div>
   }
export default CommunityActivity
