import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import NavLink from "./links";
import hmbur from '../../image/hmbur.png'
import log from '../../image/log.jpg'
import { header } from "express-validator";


class Nav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // navbar:hamburger,
      checkbox: true,
      style: {},
      
    };
    this.linksForMap = <div id = "linksForMap" >
    <Link id='abuot' to="/abuot" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='synagogue' to="/synagogue" onClick={()=>{window.scrollTo(0,0)}} />
    <Link id='checkingTM' to="/checkingTM" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='write' to="/write" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='Kitchen' to="/Kitchen" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='Azkarot' to="/Azkarot" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='Housewarming' to="/Housewarming" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='consultation' to="/consultation" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='news' to="/news" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='youth' to="/youth" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='children' to="/children" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='kindergarten' to="/kindergarten" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='women' to="/women" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='Family' to="/Family" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='needed' to="/needed" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='partner' to="/partner" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='use' to="/use" onClick={()=>{window.scrollTo(0,0)}}/>
    <Link id='connected' to="/connected" onClick={()=>{window.scrollTo(0,0)}}/>
</div>
    this.sleep = this.sleep.bind(this)
  } 

  componentDidMount() {
    this.HederBackgruond()
    window.addEventListener('scroll', this.HederBackgruond);
    // window.onhashchange = (event)=>{
    //   this.sleep(1000); 
    //   console.log(window.location.href)
    //   // window.location.href = window.location.href
    //   // console.log(event, 777);
    //   // window.location.reload(event)
    // };
      }
      
      

sleep(milliseconds) {
  var start = new Date().getTime();
  for (var i = 0; i < 1e7; i++) {
    if ((new Date().getTime() - start) > milliseconds){
      break;
    }
  }
}
HederBackgruond(){
  document.getElementById("navbar").checked = false

  let el = document.querySelector('html')

            if(el.scrollTop > 1){
              document.getElementById("logoDiv").style.boxShadow = " 0px 3px 8px 0px #505050"
              
            }else{
              document.getElementById("logoDiv").style.boxShadow = "none"

            }

}



  render(){
    return<div >
        <div id='logoDiv'><div className='container'>
              <Link to="/" ><img 
        src={log} className='logo'  alt='חב"ד אדם' onClick={()=>{window.scrollTo(0,0)}}></img>
     </Link>
     <label for='navbar' className='navbar'><img className='navbarLogo' src={hmbur} alt=''/></label>
            <input type='checkbox' id='navbar'/>
            <ul id='menu'>
            <li className="navlink">
            <a style={{textDecoration:"none"}} href='https://he.chabad.org/therebbe/default_cdo/jewish/page.htm'>
              <p className='textNL'>הרבי מליובאוויטש</p></a></li>
            <NavLink id="abuot" link='/abuot' title='אודותינו'/>
            <NavLink id="needed" link='/needed' title="עזרה לנזקקים"/>
            <NavLink id="partner" link='/partner' title='היה שותף'/>
            <NavLink id="connected" link='/connected' title='צור קשר'/>
            </ul> </div>
    </div>
    {this.linksForMap}
    </div>

}
}
export default Nav