import React from 'react';

const Connected = ({nameOC, phoneOC, emailOC, OnClick})=>{

    return<div className='mainForm'>
        <form action='menachem1414@gmail.com' method='email' encType='text/plain'>  

        <input type='text' onChange={nameOC} placeholder="שם פרטי ושם משפחה"/>
        <input type='text' onChange={phoneOC} placeholder="מספר טלפון"/>
        <input type='text' onChange={emailOC} placeholder="אימייל"/>
        <br/>
        <input type='submit' onClick={OnClick} className='submit'/>
       </form>
    </div>
}
export default Connected;
