import React from 'react';
import { Link } from 'react-router-dom';

const New = ({news = []})=>{
    
    const newList = news.map((event)=>{
        return<div className='newsDivmain'><Link to={event.link} style={{
            textDecoration:'none',
            color: "black",
        }}><div className='newsDiv'>
            <h1>{event.title}</h1>
            <div className='newImg'><img src={event.image}
            style={{width:"100%"}} alt=""></img></div>
            <p cless='newP'>{event.text}</p>
            <p>{event.date}</p>
    
        </div></Link></div>
    });

    return<ul>{newList}</ul>
}

export default New;