import React from "react";
import { Route } from "react-router-dom";
import Main from './../views/main';
import Synagogue from '../views/synagogue';
import JewIcons from './../views/JewishMatter';
import Kindergarten from './../views/kindergarten';
import Checking from './../views/checkingTM';
import Azkarot from './../views/peges/page/azkarot';
import Housewarming from './../views/peges/page/יousewarming';
import Kitchen from './../views/peges/page/kitchen';
import Family from "../views/peges/page/family";
import Abuot from './../views/abuot';
import Partner from './../views/peges/page/partner';
import Needed from './../views/needed';
import Writerebe from './../views/peges/page/writeToRebe';
import Woman from './../views/peges/page/women';
import Youth from './../views/peges/page/youth';
import Consultation from './../views/peges/page/consultation';
import Children from "../views/peges/page/children";
import Use from './../views/peges/page/use';
import Connected from './../views/peges/page/connected';
import NewsPage from './../views/peges/page/newsPage';
import CommunityActivity from './../views/בommunityActivity';


const Rout = ()=>{

    return<div> 
        <Route path="/community-activity" component={CommunityActivity}  />
        <Route path="/JewishMatter" component={JewIcons} />
        <Route path="/news" component={NewsPage} />
        <Route path="/connected" component={Connected} />
        <Route path="/needed" exact component={Needed} />
        <Route path="/partner" exact component={Partner} />
        <Route path="/synagogue" component={Synagogue} />
        <Route path="/consultation" exact component={Consultation} />
        <Route path="/children" exact component={Children} />
        <Route path="/use" exact component={Use} />
        <Route path="/checkingTM" component={Checking} />
        <Route path="/kindergarten" component={Kindergarten} />
        <Route path="/write" exact component={Writerebe} />
        <Route path="/youth" exact component={Youth} />
        <Route path="/women" exact component={Woman} />
        <Route path="/abuot" exact component={Abuot} />
        <Route path="/Azkarot" component={Azkarot} />
        <Route path="/Housewarming" component={Housewarming} />
        <Route path="/Kitchen" component={Kitchen} />
        <Route path="/Family" component={Family} />
        <Route path="/" exact component={Main} />
    
    </div>
}

export default Rout;
