import React from 'react';
import { Link } from 'react-router-dom';
import ReactDOM from 'react-dom';
import App from './../../App';

const SiteMapLink = ({link, title, id})=> {
  return (<li  className="siteMaplink">
            {/* <p style={{textDecoration:"none", color: "white"}} to={link}
            onClick={()=>{window.scrollTo(0,0) 
              document.getElementById(id).click();
              }}> */}
              <p className='textSML' onClick={()=>{window.scrollTo(0,0) 
              document.getElementById(id).click();
              }} >{title}</p>
              {/* </p> */}
            
  </li>)};

export default SiteMapLink;