import React from 'react';

const BottomLinks = ({link,src,text,alt})=> {
    alt=alt||"";

    return (
       <div className="linkBottom">
       <a href={link} style={{textDecoration:"none", color:"black", }}>
           <span className='mobile'><img className="imgLinkBottom" src={src} alt={alt}/></span>
           <span className='bigScreen'><p className="pbottom">{text}</p></span>
       </a>
       </div>
  );
}
export default BottomLinks;
